import React, { useState, useEffect } from 'react';

import Box, { BoxProps } from '@mui/joy/Box';
import Tooltip from '@mui/joy/Tooltip';
import IconButton from '@mui/joy/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import GitHubIcon from '@mui/icons-material/GitHub';

import Container from '../components/Container';

import * as style from './AppHeader.module.less';

interface AppHeaderProps extends BoxProps {
  title: string;
  isArticle: boolean;
  isFloating: boolean;
  hasSidebar: boolean;
  handleDrawerToggle: () => void;
  sx?: any;
}

const GITHUB_REPO = 'https://github.com/memset0/blog';

export default (function Header(props) {
  const { isArticle, isFloating, hasSidebar, handleDrawerToggle } = props;
  const [isTop, setIsTop] = useState(true);

  const handleScroll = () => setIsTop(document.documentElement.scrollTop <= 150);
  useEffect(() => {
    handleScroll();
    window.addEventListener('scroll', handleScroll);
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  });

  return (
    <Box component="header" className={isFloating ? style.headerFloating : style.header} sx={[{ p: 2, gap: 2 }, !isTop && { top: '0px !important' }, ...(Array.isArray(props.sx) ? props.sx : [props.sx])]}>
      <Container sx={{ display: 'flex' }}>
        <Box className={style.headerIcon} sx={{ display: 'inline-block', mr: 1 }}>
          <IconButton sx={{ display: { xs: 'flex', md: 'none' } }} component="button" color="primary" onClick={handleDrawerToggle} data-ga-event-category="header" data-ga-event-action="github">
            <MenuIcon fontSize="small" />
          </IconButton>
          <Box sx={{ display: { xs: 'none', md: 'block' }, width: '40px', height: '40px' }}></Box>
        </Box>
        <Box className={style.headerTitle}>{props.title}</Box>
        <Box className={style.headerIcon} sx={{ display: { xs: 'none', sm: 'inline-block' }, ml: 1 }}>
          <Tooltip title={'Github Repo'} enterDelay={300}>
            <IconButton component="a" color="primary" href={GITHUB_REPO} target="_blank">
              <GitHubIcon fontSize="small" />
            </IconButton>
          </Tooltip>
        </Box>
      </Container>
    </Box>
  );
} as React.FC<AppHeaderProps>);
