import React from 'react';
import JoyContainer from '@mui/joy/Container';

interface ContainerProps {
  sx?: any;
  children?: any;
}

export default (function Container(props) {
  return (
    <JoyContainer
      sx={[
        {
          paddingLeft: '8px',
          paddingRight: '8px',
        },
        ...(Array.isArray(props.sx) ? props.sx : [props.sx]),
      ]}
    >
      {props.children}
    </JoyContainer>
  );
} as React.FC<ContainerProps>);
