import React, { useState, useEffect } from 'react';

import Box from '@mui/joy/Box';
import Drawer from '@mui/material/Drawer';
import Container from '../components/Container';
import AppHeader from './AppHeader';
import AppDrawer from './AppDrawer';
import AppFooter from './AppFooter';

import siteMetadata from '../data/siteMetadata';

export interface AppContainerProps {
  title?: string;
  isArticle?: boolean;
  isHeaderFloating?: boolean;
  children?: any;
  sx?: any;
}

const DRAWER_WIDTH = 300;

const AppContainer: React.FC<AppContainerProps> = function (props) {
  const { children } = props;
  const title = props.title || '';
  const isArticle = props.isArticle || false;
  const isHeaderFloating = props.isHeaderFloating || false;

  const [hasSidebar, setHasSidebar] = useState(false);
  const [mobileOpen, setMobileOpen] = React.useState(false);

  const container = typeof window !== 'undefined' && window !== undefined ? () => window.document.body : undefined;

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  useEffect(() => {
    document.title = title ? `${title} - ${siteMetadata.title}` : siteMetadata.title;
  });

  return (
    <Box>
      <AppHeader
        title={title || siteMetadata.title}
        isArticle={isArticle}
        isFloating={isHeaderFloating}
        hasSidebar={hasSidebar}
        handleDrawerToggle={handleDrawerToggle}
        sx={{
          width: { md: `calc(100% - ${DRAWER_WIDTH}px)` },
          ml: { md: `${DRAWER_WIDTH}px` },
        }}
      ></AppHeader>
      <Box component="nav" sx={{ width: { md: DRAWER_WIDTH }, flexShrink: { md: 0 } }} aria-label="mailbox folders">
        <Drawer
          container={container}
          variant="temporary"
          open={mobileOpen}
          onClose={handleDrawerToggle}
          ModalProps={{
            keepMounted: true,
          }}
          sx={{
            display: { xs: 'block', md: 'none' },
            '& .MuiDrawer-paper': { boxSizing: 'border-box', width: DRAWER_WIDTH },
          }}
        >
          <AppDrawer />
        </Drawer>
        <Drawer
          variant="permanent"
          sx={{
            display: { xs: 'none', md: 'block' },
            '& .MuiDrawer-paper': { boxSizing: 'border-box', width: DRAWER_WIDTH },
          }}
          open
        >
          <AppDrawer />
        </Drawer>
      </Box>
      <Box
        sx={{
          width: { xs: '100%', md: `calc(100% - ${DRAWER_WIDTH}px)` },
          ml: { xs: 'auto', md: `${DRAWER_WIDTH}px` },
        }}
      >
        <Container sx={props.sx}>{children}</Container>
      </Box>
      <AppFooter></AppFooter>
    </Box>
  );
};

export default AppContainer;
